import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';

import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import { isMobile } from 'react-device-detect';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';

import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluirLogo.png';
import BegContent from './provContent/beg2';
import VacunasContent from './provContent/vacunas';
import BilleteraContent from './provContent/billetera';
import Incluir from './provContent/incluir';
import Zoom from '@material-ui/core/Zoom';
import colores from '../styles/colors';
import { Grid } from '@material-ui/core';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

export default function VerticalTabs() {
    const classes = useStyles();
    const [value, setValue] = React.useState(3);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Grid container spacing={0} className={classes.root}>
            <Grid item sm={1} style={{ maxHeight: 570, paddingTop: 60 }}>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    aria-label="Vertical tabs example"
                    className={classes.tabs}
                    indicatorColor={'primary'}
                >
                    {provBtn.map((btn, i) => (
                        <ProvCard
                            i={i}
                            btn={btn}
                            btnProv={value}
                            setBtnProv={setValue}
                        />
                    ))}
                </Tabs>
            </Grid>
            <Grid item sm={11} style={{ maxHeight: '90vh', overflow: 'auto' }}>
                {provBtn.map((tab, i) => (
                    <TabPanel value={value} index={i}>
                        <Typography style={{ margin: 10, fontSize: 28 }}>
                            {tab.title}
                        </Typography>
                        {tab.content}
                    </TabPanel>
                ))}
            </Grid>
        </Grid>
    );
}

const ProvCard = ({ btn, btnProv, setBtnProv, i }) => {
    return (
        <Zoom in={true} timeout={300 * i + 400}>
            <Card
                onClick={() => setBtnProv(i)}
                elevation={3}
                style={{
                    margin: 10,
                    backgroundColor:
                        i === btnProv ? colores.letrajor08 : '#f2f2f2',
                    marginBottom: 6,
                    padding: 6,
                    borderRadius: '50%',
                    height: 90,
                    width: isMobile ? '25%' : 90,
                    alignSelf: 'center',
                    cursor: 'pointer'
                }}
            >
                <CardMedia
                    style={{ height: '100%', borderRadius: '50%' }}
                    image={btn.img}
                    title={btn.name}
                />
            </Card>
        </Zoom>
    );
};

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        display: 'flex',
        overflow: 'hidden'
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
        overflow: 'hidden',
        maxHeight: 570
    }
}));

const provBtn = [
    {
        id: 'billetera',
        title: 'Billetera Santa Fe',
        img: billetera,
        name: 'Billetera Santa Fe',
        content: <BilleteraContent />
    },
    {
        id: 'beg',
        title: 'Boleto Educativo Gratuito',
        img: begIcon,
        name: 'Boleto Educativo Gratuito',
        content: <BegContent />
    },

    {
        id: 'incluir',
        title: 'Programa Incluir',
        img: incluir,
        name: 'Plan Incluir',
        content: <Incluir />
    },
    {
        id: 'vacuna',
        title: 'Plan Provincial Santa Fe Vacuna contra el Covid',
        img: vacuna,
        name: 'Plan de Vacunacion',
        content: <VacunasContent />
    }
];
