import React, { useCallback, useContext, useEffect, useState } from 'react';
import { isMobile, isIOS } from 'react-device-detect';
import PaperText from '../components/paperText';
import {
    Avatar,
    Card,
    CardActionArea,
    CardMedia,
    Chip,
    Fade,
    Grid,
    makeStyles,
    Snackbar,
    Tooltip,
    Typography,
    Zoom
} from '@material-ui/core';
import '../pages/globals.css';
import NavigationBar from '../components/navigation';
import SocialButton from '../components/socialButton';
import buscanlogo from '../assets/buscanlogo.png';
import MobileMenu from '../components/mobileMenu';
import jorL from '../assets/jorLogo.png';
import hacemosL from '../assets/ejesProv/hsf.jpg';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import jorFondo from '../assets/fondo/jorfindo.jpg';
import useProgressiveImage from '../helpers/progressiveImg';

import NoticiasView from './noticiasView';
import Loader from '../components/loader';
import colores from '../styles/colors';
import { Link } from 'react-router-dom';
import { Alert } from '@material-ui/lab';
import { FormContext } from '../context/formContext';

export default function Home() {
    const { handlerModal } = useContext(FormContext);
    const [openSnack, setopenSnack] = useState(false);
    const [dblClick, setDblClick] = useState(false);
    const [openMenu, setOpenMenu] = useState(false);
    const styles = useStyles();

    const imag = useProgressiveImage(jorFondo);

    const onBackButtonEvent = useCallback((e) => {
        e.preventDefault();

        if (dblClick) return window.history.back();
        setopenSnack(true);
        setDblClick(true);
        setTimeout(() => {
            setDblClick(false);
        }, 400);
    }, []);

    useEffect(() => {
        window.history.pushState(null, null, window.location.pathname);

        window.addEventListener('popstate', onBackButtonEvent);

        return () => window.removeEventListener('popstate', onBackButtonEvent);
    }, [onBackButtonEvent]);

    if (!imag) {
        return <Loader />;
    }

    return (
        <>
            <Snackbar
                anchorOrigin={{
                    vertical: isMobile ? 'bottom' : 'top',
                    horizontal: 'left'
                }}
                open={openSnack}
                autoHideDuration={3000}
                onClose={() => setopenSnack(false)}
            >
                <Alert severity="info">clickea 2 veces para salir</Alert>
            </Snackbar>
            <Fade in={true} timeout={600}>
                <div
                    className={styles.fullscreen}
                    style={{ backgroundImage: `url(${imag})` }}
                >
                    <div className={styles.floatTop}>
                        {!isMobile ? (
                            <SocialButton />
                        ) : (
                            <MobileMenu
                                openMenu={openMenu}
                                setOpenMenu={setOpenMenu}
                            />
                        )}
                    </div>

                    {isMobile && (
                        <div
                            style={{
                                position: 'absolute',
                                top: 28,
                                left: 95
                            }}
                        >
                            <img
                                alt="jorgelina mudallel logo"
                                src={jorL}
                                style={{ height: 85, marginBottom: 10 }}
                            />
                        </div>
                    )}

                    {!isMobile && <DirectAcc />}

                    <div className={styles.descJor}>
                        {!isMobile && (
                            <img
                                alt="jorgelina mudallel"
                                src={jorL}
                                style={{
                                    height: isMobile ? 85 : 150,
                                    marginBottom: 25
                                }}
                            />
                        )}

                        {isMobile && (
                            <ChipsDirect
                                handlerModal={handlerModal}
                                setOpenMenu={setOpenMenu}
                            />
                        )}
                        {!isMobile && (
                            <PropuestaCont handlerModal={handlerModal} />
                        )}
                        <PaperText />
                    </div>

                    {!isMobile && <NoticiasView />}
                    {!isMobile && <NavigationBar />}
                </div>
            </Fade>
        </>
    );
}

const ChipsDirect = ({ handlerModal, setOpenMenu }) => {
    return (
        <div
            style={{
                flexDirection: 'row-reverse',
                display: 'flex',
                justifyContent: 'space-between'
            }}
        >
            <div>
                {directAcc.map((btn, i) => (
                    <Zoom in={true} timeout={500}>
                        <Chip
                            key={i}
                            onClick={() => {
                                window.open(btn.url);
                            }}
                            avatar={<Avatar src={btn.img} />}
                            style={{ margin: 2 }}
                        />
                    </Zoom>
                ))}
            </div>
            <div
                style={{
                    display: 'flex',
                    flexDirection: 'row-reverse'
                }}
            >
                <Zoom in={true} timeout={500}>
                    <Chip
                        onClick={() => {
                            handlerModal({
                                id: 'propuestas',
                                name: 'Mis Propuestas',
                                icon: ''
                            });
                            setOpenMenu(true);
                        }}
                        key={'propuestas'}
                        label="MIS PROPUESTAS"
                        style={{
                            fontSize: 12,
                            backgroundColor: colores.letrajor,
                            color: '#f2f2f2'
                        }}
                    />
                </Zoom>
            </div>
        </div>
    );
};

const DirectAcc = () => {
    const classes = useStyles();
    return (
        <div
            style={{
                position: 'absolute',
                right: 55,
                top: 55,
                flexDirection: 'column',
                display: 'flex'
            }}
        >
            <Tooltip key="hsf" title="Sumate a Hacemos Santa Fe">
                <Card className={classes.cardHSF}>
                    <CardActionArea>
                        <Link to={'/sumate'}>
                            <CardMedia
                                style={{
                                    height: 140,
                                    borderRadius: '25%'
                                }}
                                image={hacemosL}
                            />

                            <Chip
                                className={classes.chipHSF}
                                label="SUMATE"
                                variant="outlined"
                            />
                        </Link>
                    </CardActionArea>
                </Card>
            </Tooltip>
            <div
                style={{
                    width: 140
                }}
            >
                <Grid container spacing={1}>
                    {directAcc.map((btn, i) => (
                        <Zoom key={btn.title} in={true} timeout={300 * i + 500}>
                            <Grid item sm={4}>
                                <Tooltip key={btn.title} title={btn.title}>
                                    <Card
                                        key={i}
                                        className={classes.accesodirectoweb}
                                        onClick={() => {
                                            window.open(btn.url);
                                        }}
                                    >
                                        <CardActionArea>
                                            <CardMedia
                                                style={{
                                                    height: 40
                                                }}
                                                image={btn.img}
                                            />
                                        </CardActionArea>
                                    </Card>
                                </Tooltip>
                            </Grid>
                        </Zoom>
                    ))}
                </Grid>
            </div>
        </div>
    );
};

const directAcc = [
    {
        id: 'BEG',
        img: begIcon,
        url: 'https://sso.santafe.gov.ar/service-auth/login?service=https://www.santafe.gob.ar/boletoeducativo/login',
        title: 'inicia el registro'
    },
    {
        id: 'billetera',
        img: billetera,
        url: isIOS
            ? 'https://apps.apple.com/ar/app/billetera-santa-fe/id1558196556'
            : 'https://play.google.com/store/apps/details?id=com.pluspagos.mobile',
        title: 'descarga la app'
    },

    {
        id: 'buscan',
        img: buscanlogo,
        url: 'https://play.google.com/store/apps/details?id=com.colodev.buscan',
        title: 'BusCan: descarga la app'
    }
    /* {
        id: 'vacunas',
        img: vacuna,
        url: 'https://www.santafe.gob.ar/santafevacunacovid/inicio',
        title: 'inscribite y recibi la vacuna'
    } */
];

const PropuestaCont = ({ handlerModal }) => {
    const styles = useStyles();
    return (
        <div className={styles.divpropuestas}>
            <Card
                onClick={() =>
                    handlerModal({
                        id: 'propuestas',
                        name: 'Mis Propuestas',
                        icon: ''
                    })
                }
                elevation={3}
                className={styles.propuestascard}
            >
                <Typography className={styles.textpropuesta}>
                    MIS PROPUESTAS
                </Typography>
            </Card>
        </div>
    );
};

