import React, { useContext, useState } from 'react';
import { makeStyles, Zoom } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import colores from '../styles/colors';
import NestedList from './menuList';
import jorLogo from '../assets/jorLogo.png';
import { FormContext } from '../context/formContext';

const MobileMenu = ({openMenu, setOpenMenu}) => {
    const { handlerModal } = useContext(FormContext);
    const classes = useStyles();
    

    return (
        <>
            <Zoom in={true} timeout={1100}>
                <Fab onClick={() => setOpenMenu(true)}>
                    <MenuIcon />
                </Fab>
            </Zoom>
            <Drawer
                classes={{ paper: classes.drawer }}
                anchor="left"
                open={openMenu}
                onClose={() => setOpenMenu(false)}
            >
                <div className={classes.content}>
                    <div
                        style={{
                            height: 85,
                            margin: 25
                        }}
                    >
                        <img
                            src={jorLogo}
                            alt="jorLogo"
                            style={{ height: '100%' }}
                        />
                    </div>

                    <NestedList handlerModal={handlerModal} />
                </div>
            </Drawer>
        </>
    );
};

const useStyles = makeStyles((theme) => ({
    drawer: {
        backgroundColor: '#f2f2f2'
    },
    content: {},
    exitBtn: { backgroundColor: colores.mainDark, margin: 20 }
}));

export default MobileMenu;
