import React from 'react';

import {
    Paper,
    Grid,
    Typography,
    Card,
    CardMedia,
    CircularProgress,
    Fade
} from '@material-ui/core';
import provStyles from '../../styles/provStyle';
import { incluirData } from '../../info/provincia';

import { isMobile } from 'react-device-detect';
import useProgressiveImage from '../../helpers/progressiveImg';

const VacunasContent = () => {
    const classes = provStyles();
    const imag = useProgressiveImage(incluirData.media1.img);

    if (!imag) {
        return (
            <CircularProgress
                style={{
                    color: '#00AFE8',
                    marginTop: isMobile ? 20 : 80,
                    marginLeft: !isMobile && '40%'
                }}
                size={isMobile ? 40 : 70}
            />
        );
    }
    return (
        <Fade in={true} timeout={300}>
            <div className={classes.marginContainer}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={12}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 120 : 220,
                                borderRadius: 15
                            }}
                        >
                            <CardMedia
                                image={imag}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Grid>
                    {isMobile && (
                        <Typography style={{ fontSize: 22, margin: 'auto' }}>
                            Programa Incluir
                        </Typography>
                    )}
                    <Grid item sm={8}>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {incluirData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>

                    <Grid item xs={12} sm={4}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 110 : '100%',
                                borderRadius: 15
                            }}
                        >
                            <CardMedia
                                image={incluirData.media2.img}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 110 : 220,
                                borderRadius: 15
                            }}
                        >
                            <CardMedia
                                image={incluirData.media4.img}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 110 : '100%',
                                borderRadius: 15
                            }}
                        >
                            <CardMedia
                                image={incluirData.media3.img}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Grid>
                </Grid>
            </div>
        </Fade>
    );
};

export default VacunasContent;
