import React, { useContext } from 'react';

import {
    Paper,
    Grid,
    Typography,
    Card,
    CardMedia,
    CircularProgress,
    Fade
} from '@material-ui/core';
import provStyles from '../../styles/provStyle';
import { vacunasData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';

import useProgressiveImage from '../../helpers/progressiveImg';
import { FormContext } from '../../context/formContext';

const VacunasContent = () => {
    const { vacunas } = useContext(FormContext);
    const classes = provStyles();
    const imag = useProgressiveImage(vacunasData.media1.img);

    if (!imag) {
        return (
            <CircularProgress
                style={{
                    color: '#00AFE8',
                    marginTop: isMobile ? 20 : 80,
                    marginLeft: !isMobile && '40%'
                }}
                size={isMobile ? 40 : 70}
            />
        );
    }

    return (
        <Fade in={true} timeout={300}>
            <div className={classes.marginContainer}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={10}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 130 : 240,
                                borderRadius: 15,
                                margin: 'auto',
                                marginTop: 10
                            }}
                        >
                            <img
                                alt="Plan Vacuna Santa Fe Covid"
                                src={imag}
                                style={{
                                    width: '100%',
                                    marginTop: isMobile ? '-14%' : '-20%',
                                    paddingTop: '10%'
                                }}
                            />
                        </Card>
                    </Grid>
                    {isMobile && (
                        <Typography
                            style={{
                                fontSize: 22,
                                margin: 'auto',
                                marginTop: 15
                            }}
                        >
                            Plan Provincial Santa Fe Vacuna contra el Covid
                        </Typography>
                    )}
                    <Grid item xs={12} sm={2}>
                        <Paper
                            elevation={3}
                            className={classes.cardVert}
                            style={{
                                height: isMobile ? '90%' : '100%',
                                textAlign: 'center',
                                paddingTop: isMobile ? 20 : '26%'
                            }}
                            onClick={() =>
                                window.open(
                                    'https://www.santafe.gob.ar/santafevacunacovid/inicio'
                                )
                            }
                        >
                            <Typography
                                className={classes.textmed}
                                style={{ marginBottom: 10 }}
                            >
                                {vacunasData.card0.txtA}
                            </Typography>
                            {vacunasData.card0.ico}
                            <Typography className={classes.textGrl}>
                                {vacunasData.card0.txtB}
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} sm={9}>
                        {vacunas && <DatosVac vacunas={vacunas} />}
                    </Grid>

                    <Grid item xs={12} sm={3}>
                        <Card
                            elevation={5}
                            style={{ height: '100%', borderRadius: 15 }}
                        >
                            <CardMedia
                                image={vacunasData.media2.img}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} sm={6}>
                                <Paper
                                    elevation={3}
                                    className={classes.cards}
                                    style={{ height: '100%' }}
                                >
                                    <Typography className={classes.textTitle}>
                                        {vacunasData.text}
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Card
                                    elevation={5}
                                    style={{
                                        borderRadius: 15,
                                        height: isMobile ? 180 : '100%'
                                    }}
                                >
                                    <CardMedia
                                        image={vacunasData.media4.img}
                                        style={{ height: '100%' }}
                                    />
                                </Card>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Card
                                    elevation={5}
                                    style={{
                                        borderRadius: 15,
                                        height: isMobile ? 180 : '100%'
                                    }}
                                >
                                    <CardMedia
                                        image={vacunasData.media3.img}
                                        style={{ height: '100%' }}
                                    />
                                </Card>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Paper
                                    elevation={3}
                                    className={classes.cards}
                                    style={{ height: '100%' }}
                                >
                                    <Typography className={classes.textTitle}>
                                        {vacunasData.card2.txtA}
                                    </Typography>
                                    <Typography className={classes.textmed}>
                                        {vacunasData.card2.txtB}
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Paper elevation={3} className={classes.cards}>
                                    <Typography className={classes.textTitle}>
                                        {vacunasData.card5.txtA}
                                    </Typography>
                                    <Typography className={classes.textmed}>
                                        {vacunasData.card5.txtB}
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Paper elevation={3} className={classes.cards}>
                                    <Typography className={classes.textTitle}>
                                        {vacunasData.card1.txtA}
                                    </Typography>
                                    <Typography className={classes.textmed}>
                                        {vacunasData.card1.txtB}
                                    </Typography>
                                    <Typography className={classes.textmed}>
                                        {vacunasData.card1.txtC}
                                    </Typography>
                                </Paper>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </Fade>
    );
};

const DatosVac = ({ vacunas }) => {
    const classes = provStyles();
    console.log('vac fin ', vacunas);
    if (!vacunas.total) return <></>;

    return (
        <Grid
            container
            spacing={3}
            style={{ padding: 10, textAlign: 'center' }}
        >
            <Grid item xs={12} sm={12}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textMini}>
                        ultima actualizacion: {vacunas.fecha}
                    </Typography>
                    <Typography className={classes.textTitle}>
                        TOTAL DE VACUNAS APLICADAS EN LA PROVINCIA DE SANTA FE
                    </Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.total.toString().slice(0, 1)}.
                        {vacunas.total.toString().slice(1, 4)}.
                        {vacunas.total.toString().slice(4)}
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        EN EL ULTIMO DIA
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12} sm={8}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <Card elevation={3} className={classes.cardVacunaDatos}>
                            <Typography className={classes.textmed}>
                                CON 1 DOSIS
                            </Typography>
                            <Typography className={classes.textBig}>
                                {vacunas.unadosis}
                            </Typography>
                        </Card>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Card elevation={3} className={classes.cardVacunaDatos}>
                            <Typography className={classes.textmed}>
                                CON 2 DOSIS
                            </Typography>
                            <Typography className={classes.textBig}>
                                {vacunas.dosdosis}
                            </Typography>
                        </Card>
                    </Grid>
                </Grid>
            </Grid>

            <Grid item xs={12} sm={4}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        EN EL ULTIMO DIA
                    </Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.totall}
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        TOTALES POR VACUNA
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12} sm={3}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        ASTRAZENECA
                    </Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.astrazeneca.toString().slice(0, 1)}.
                        {vacunas.astrazeneca.toString().slice(1, 4)}.
                        {vacunas.astrazeneca.toString().slice(4)}
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12} sm={3}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        SINOPHARM
                    </Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.sinopharm.toString().slice(0, 1)}.
                        {vacunas.sinopharm.toString().slice(1, 4)}.
                        {vacunas.sinopharm.toString().slice(4)}
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12} sm={3}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>
                        Sputnik-V
                    </Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.sputnik.toString().slice(0, 1)}.
                        {vacunas.sputnik.toString().slice(1, 4)}.
                        {vacunas.sputnik.toString().slice(4)}
                    </Typography>
                </Card>
            </Grid>
            <Grid item xs={12} sm={3}>
                <Card elevation={3} className={classes.cardVacunaDatos}>
                    <Typography className={classes.textmed}>MODERNA</Typography>
                    <Typography className={classes.textBig}>
                        {vacunas.moderna.toString().slice(0, 3)}.
                        {vacunas.moderna.toString().slice(3)}
                    </Typography>
                </Card>
            </Grid>
        </Grid>
    );
};

export default VacunasContent;
